<template>
  <div id="app">
    <h1>Latest Vue.js Commits</h1>
    <template v-for='branch in branches'>
      <input type="radio" :name="branch" :id="branch" :value="branch" v-model="currentBranch">
        <label :for="branch">{{branch}}</label>
    </template>
    <p>vuejs/vue@{{currentBranch}}</p>
    <ul>
        <li v-for="record in commits">
            <a :href='record.html_url' class="commit">{{record.sha.slice(0,7)}}</a>
            - <span class="message">{{record.commit.message }}</span><br>
            by <span class="author">{{record.commit.author.name}}</span>
            at <span class="date">{{record.commit.author.date}}</span>
        </li>
    </ul>
  </div>
  <Test1></Test1>
</template>

<script>
// import Hello from './components/Hello'
import Test1 from './components/Test1'
var apiURL = 'https://api.github.com/repos/vuejs/vue/commits?per_page=3&sha='

export default {
  data () {
    return {
      input: 'ok',
      branches: ['master', 'dev'],
      currentBranch: 'master',
      commits: null
    }
  },
  created () {
    this.fetchDate()
  },
  watch: {
    currentBranch: 'fetchDate'
  },
  methods: {
    fetchDate () {
      let xhr = new XMLHttpRequest()
      let self = this
      xhr.open('GET', apiURL + self.currentBranch)
      xhr.onload = function () {
        self.commits = JSON.parse(xhr.responseText)
      }
      xhr.send()
    }
  },
  props: {
    show: {
      type: Boolean,
      required: true,
      twoWay: true
    }
  },
  components: {
    // Hello
    Test1
  }
}
</script>

<style>
body {
  font-family: Helvetica, sans-serif;
}
</style>



